.form-admin{
    padding: 2rem 0 0 0;
    margin: 2rem 25%;
    background-color: #0c0c0c1c;
    @include telefono{
        margin: 0rem;
        background-color: #0000001e;
    }
    form label{
        color: $negro;
        font-weight: bold;
        margin-left: 2rem;

    }
    form input[type="password"],
    form input[type="email"]{
      width:18rem;
      height: 1.8rem;
      font-size: 0.9rem;
      padding:3px 10px;
      border:1px solid #f6f6f6;
      border-radius:0.2rem;
      background-color:#f6f6f6;
      margin:0.3rem 0 0 3rem;
      display:inline-block;
      @include telefono{
        width: 80%;
        display: inline;
        margin:0.5rem;
       
      }

    }
    form input[type="email"]{
        margin-left: 5rem;
        @include telefono{
            display: inline;
            margin:0.5rem;
            
          }
    }
    form button{
        margin-top: 2rem;
        margin-left: 80%;
        margin-bottom: 2rem;
        background-color: #297bc9c7;
        border: none;
        color: white;
        padding: 15px 32px;
        text-align: center;
        text-decoration: none;
        display: inline-block;
        font-size: 16px;
        cursor: pointer;
        @include telefono{
          
            margin-left:5rem;
           
          }
    }
  
}
.center{
    margin-left: 6rem;
    a{
        margin-right: 3rem;
    }
    a:hover{
        color: rgb(255, 255, 255);
    }
}
.actualizar-eliminar{
    width: 15%;
    margin-right: 0rem;
    text-align: center;
    font-size: 1.2rem;
    
}
.container-img{
nav{
 margin-left: 2.9rem;
 text-align: center;
 a{
     margin-right: 0.9rem;
    padding-left: 2rem;
    padding-right: 4rem;
 }
 a:hover{
     color: rgba(185, 185, 174, 0.993);
 }
}
}