.titulo-crear{
    margin: 1rem 40%;
}
.container-formulario{
    margin: 1rem 20%;

    fieldset{
        margin: 0.6rem 0.1rem;
    }
    form input[type="text"],
    form input[type="number"],
    form input[type="file"],
    textarea{
        margin: 1rem 0.2rem;
    }

        form input[type="text"]{
        width: 90%;
    }
    legend{
        margin: 0.3rem 0;
        font-size: 1rem;
    }
    label{
        margin: 0.8rem 2rem 0.2rem 0.5rem;
        font-size: 1.2rem;
        font-weight: bold;
        text-transform: uppercase;

    }
    textarea {
        width: 90%;
        resize: none;
    }
}