}
.container-form{
  margin: 0 0.5rem;
  padding: .6rem;
  
   @include grid(2,0.6rem);
      
    @include telefono{
      display:grid;
      grid-template-rows:1.2fr 2fr;
      grid-template-columns:none;
     
    }
   
    .info{
      font-size: 1.3rem;
      font-weight: 500;
      font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
      margin-left: 6rem;
