      display:inline-block;
      @include tablet{
        width: 50%;
        margin:0.1rem 0 0.4rem 0rem;
      }

    }
    form textarea,
    form button{

      border-radius:3px;
      width:18.9rem;
      margin:0.1rem 0 0.4rem 8rem;
      display:inline-block;
      resize: none;
      @include tablet{
        width: 50%;
        margin:0.1rem 0 0.4rem 0rem;
      }
    }
    form button{
      border-radius: 0.2rem;
    }

    .form{
    
      margin-left: 2rem;
      border-radius: 1rem;

    }
}
.container-contac{
  
  
  @include telefono{
